@use '../base/variables' as *;
@use '../base/mixins' as m;


.listado-servicios{
    display: grid;
    gap: 2rem;
    
    @include m.tablet { 
        @include m.grid(2, 2rem);
    }
    .servicio{
        background-color: $blanco;
        padding: 3rem;
        border-radius: 1rem;
        transition-property: transform;
        transition-duration: 300ms;
        text-align: center;

        @include m.tablet { 
            text-align: left;
        }

        &:hover{
            transform: scale(1.1);
        }

        p{
            color:  $negro;
            margin: 0 0 calc( $separacion / 4) 0 ;
        }
        .precio-servicio{
            color: $azul;
            font-weight: bold;
            font-size: 3rem;
            margin: 0;
        }
        &.seleccionado{
            background-color: $azul;
            p{
                color: $blanco;
            }
        }
    }
}