@use '../base/variables' as *;
@use '../base/mixins' as m;

.alerta{
    padding: calc($separacion / 2);
    color: $blanco;
    width: 100%;
    display: block;
    margin-bottom:2rem;
    text-align: center;
    font-weight: bold;
    text-transform: uppercase;

    &.error{
        background-color: $rojo;
    }
    &.exito{
        background-color: $verde;
    }
}