@use '../base/variables' as *;
@use '../base/mixins' as m;

.tabs{

    @include m.tablet { 
        display: flex;

    }

    button{
        background-color: $azul;
        border: none;
        color: $blanco;
        padding: 1rem;
        display: block;
        width: 100%;
        margin-bottom: 1rem;
        text-transform: uppercase;
        font-weight: $bold;
        transition-property:  background-color;
        transition-duration: 300ms;
        animation: ease;
        animation-duration: 300ms;

        &:hover{
            cursor: pointer;
            background-color: darken($color: $azul, $amount: 10%);
        }
        &.actual{
            background-color: $blanco;
            color: $azul;
        }
    }
}