@use '../base/variables' as *;
@use '../base/mixins' as m;


.citas{
    list-style: none;
    padding: 0;
    li{
        border-top: 1px solid $blanco;
        p{
            font-weight: $bold;
            color: $azul;
        }
        span,
        .servicio{
            font-weight: $regular;
            color: $blanco;
        }
    }
}

.servicios{
    @extend .citas;

    .acciones{
        margin: 0;
        justify-content: center;
        gap: 4rem;
        margin-bottom: 2rem;
        a,input{
            margin: 0;
        }
        a{
            margin-bottom: 1rem;
            @include m.tablet { 
                margin: 0;
            }
        }
    }
}