@use '../base/variables' as *;
@use '../base/mixins' as m;

.contenedor-app{

    .imagen{
       height: calc(50vh - 10rem);
        background-image: url(../img/1.jpg);
        background-size: cover;
        background-position: center center;
    }

    @include m.tablet { 
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;

        .imagen{
            height: 100%;
            background-size: cover;
        }
    }

    .app{
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0;
        @include m.tablet { 
            overflow:scroll;
        }
    }

}

.barra{
    margin-bottom: $separacion;
    @include m.tablet { 
        display:flex;
        justify-content: space-between;
        align-items: center;
    }
    .boton{
        @include m.boton($negro, $blanco);
        margin: 0;
    }
}

.barra-servicios{
    margin-bottom: $separacion;
    @include m.tablet { 
        display:flex;
        justify-content: space-between;
        gap: 1rem;
    }
    .boton{
        margin: 0 0 1rem 0;
        padding: 1rem .3rem;
    }
}