@use '../base/variables' as *;
@use '../base/mixins' as m;

.acciones{
    margin-top: $separacion;
    @include m.tablet { 
        display:flex;
        justify-content: space-between;
        align-items: center;
    }
    
    a{
        color: $blanco;
        display: block;
        margin-bottom: calc($separacion / 2);
        text-align: center;
    }
}